.btn {
  border: none;
  background-color: transparent;
  padding: 0;
  margin: 0;
  border-radius: 20px;
  transition: all 850ms;
  backface-visibility: hidden;
}
.btn.btn-color {
  background-color: #f6cb5e;
  text-transform: uppercase;
  padding: 8px 30px;
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 1px;
  color: #fff;
  transition: background-color 350ms;
  border: 1px solid #fff;
}
.btn:hover.btn-color {
  background-color: #f2b416;
}
.btn:focus,
.btn:active {
  outline: none;
  box-shadow: none;
}
.nav-button {
  padding: 10px 0;
  border: none;
  background: none;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  outline: none;
  display: flex;
}
.nav-button .button-bar {
  height: 1.5px;
  width: 24px;
  border-radius: 10px;
  transition: all 0.5s;
  background-color: #000;
}
.nav-button .button-bar + .button-bar {
  margin-top: 5px;
}
.nav-button:focus,
.nav-button:active {
  outline: none;
  box-shadow: none;
}
.nav-button-close {
  position: absolute;
  z-index: 11;
  width: 100%;
  top: 40px;
  left: 0;
}
.nav-button-close .container {
  display: flex;
  justify-content: flex-end;
}
.nav-button-close .container:before,
.nav-button-close .container:after {
  content: none;
}
.nav-button-close button {
  outline: none;
  border: none;
  background: none;
  padding: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav-button-close button .button-bar {
  display: block;
  position: absolute;
  width: 20px;
  height: 1px;
  background: #222;
}
.nav-button-close button .button-bar:nth-child(1) {
  transform: rotate(45deg);
}
.nav-button-close button .button-bar:nth-child(2) {
  transform: rotate(-45deg);
}
#return-to-top {
  position: fixed;
  z-index: 333;
  bottom: 80px;
  right: 0;
  display: block;
  text-decoration: none;
  visibility: hidden;
  opacity: 0;
  transition: all 350ms ease;
  cursor: pointer;
  transform: translateY(100px) rotate(90deg);
}
#return-to-top.active {
  opacity: 1;
  visibility: visible;
  transform: translateY(0) rotate(90deg);
}
#return-to-top.white .line {
  background-color: #e7e6e4;
}
#return-to-top.white p {
  color: #fff;
}
#return-to-top.white:hover p {
  color: #fff;
}
#return-to-top .line {
  width: 120px;
  height: 0.5px;
  background-color: #f6cb5e;
}
#return-to-top p {
  font-size: 15px;
  color: #000;
  text-transform: uppercase;
  text-align: right;
  margin-right: 3px;
  transition: color 350ms;
  font-weight: 600;
}
#return-to-top:hover p {
  color: #f6cb5e;
}
