body {
  min-height: 100vh;
  height: 100%;
  font-family: "Cormorant", serif;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #f2f2f2;
  transition: background-color 350ms;
}
body.no-scroll {
  overflow: hidden;
  max-height: 100vh;
}
body ul,
body ol,
body p,
body h1,
body h2,
body h3,
body h4,
body h5,
body h6 {
  margin: 0;
  padding: 0;
}
body a {
  text-decoration: none !important;
}
.alert-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  background: rgba(0, 0, 0, 0.2);
  z-index: 555;
  opacity: 0;
  visibility: hidden;
  transition: all 0.4s;
}
.alert-wrapper .alert-service {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 100%;
  transform: translateX(8px);
  padding: 50px 0;
}
.alert-wrapper .alert-service .service-inner {
  padding: 50px;
  background: #fff;
  width: 100%;
  box-shadow: 0px 0px 32px 1px rgba(0, 0, 0, 0.15);
  text-align: center;
  border-top: 5px solid #fff;
  position: relative;
  transform: scale(0.8);
  transition: all 0.5s;
}
.alert-wrapper .alert-service .service-inner .inner-dismiss {
  top: 10px;
  right: 10px;
  position: absolute;
  border: 0;
  background: none;
  padding: 0;
  cursor: pointer;
  display: flex;
  padding: 5px;
  outline: none;
}
.alert-wrapper .alert-service .service-inner .inner-dismiss img {
  height: 15px;
}
.alert-wrapper .alert-service .service-inner.success {
  border-top: 5px solid #78b042;
}
.alert-wrapper .alert-service .service-inner.warning {
  border-top: 5px solid #d51a27;
}
.alert-wrapper .alert-service .service-inner.info {
  border-top: 5px solid #1a88d5;
}
.alert-wrapper.active {
  opacity: 1;
  visibility: visible;
}
.alert-wrapper.active .alert-service .service-inner {
  transform: none;
}
.modal-service-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.3);
  z-index: 666;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s;
  overflow-x: scroll;
}
.modal-service-wrapper.active {
  opacity: 1;
  visibility: visible;
}
.modal-service-wrapper .modal-service {
  padding: 50px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100%;
  opacity: 0;
  visibility: hidden;
  transition: transform 0.4s;
}
.modal-service-wrapper .modal-service.active {
  opacity: 1;
  visibility: visible;
  transform: translateY(-40px);
}
.modal-service-wrapper .modal-service .service-inner {
  position: relative;
  background: #fff;
  min-width: 700px;
  max-width: 60%;
  padding: 50px 25px 25px 25px;
}
.modal-service-wrapper .modal-service .service-inner .service-button {
  position: absolute;
  top: 20px;
  right: 20px;
  border: none;
  background: none;
  padding: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal-service-wrapper .modal-service .service-inner .service-button .button-bar {
  display: block;
  position: absolute;
  width: 20px;
  height: 1px;
  background: #222;
}
.modal-service-wrapper .modal-service .service-inner .service-button .button-bar:nth-child(1) {
  transform: rotate(45deg);
}
.modal-service-wrapper .modal-service .service-inner .service-button .button-bar:nth-child(2) {
  transform: rotate(-45deg);
}
.modal-service-wrapper .modal-service .service-inner .service-content {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
}
.modal-service-wrapper .modal-service .service-inner .service-content .heading {
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 20px;
}
.modal-service-wrapper .modal-service .service-inner .service-content .btn {
  display: flex;
  justify-content: center;
  margin-top: 5px;
}
.modal-service-wrapper .modal-service .service-inner .service-content ul {
  list-style-type: none;
  width: 30%;
}
.modal-service-wrapper .modal-service .service-inner .service-content ul li:not(.heading) {
  font-size: 15px;
  position: relative;
  padding: 5px 0 5px 15px;
}
.modal-service-wrapper .modal-service .service-inner .service-content ul li.heading {
  font-size: 22px;
}
.modal-service-wrapper .modal-service .service-inner .service-content ul li:before {
  content: "";
  position: absolute;
  top: 20px;
  left: -5px;
  width: 5px;
  height: 5px;
  border-radius: 100px;
  background-color: #f6cb5e;
}
.modal-service-wrapper .modal-service .service-inner .service-content ul li strong {
  display: block;
  font-size: 20px;
}
.modal-service-wrapper .modal-service .service-inner .service-content ul li p {
  margin-top: 5px;
  padding-top: 5px;
  border-top: 1px solid #f6cb5e;
  font-weight: 700;
  display: block;
}
.modal-service-wrapper .modal-service .service-inner .service-buttons {
  margin-top: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.modal-service-wrapper .modal-service .service-inner .service-buttons .btn-link {
  padding: 0;
  text-transform: uppercase;
  font-weight: 700;
  text-decoration: none;
}
.select-wrapper {
  position: relative;
}
.select-wrapper label {
  color: #666;
}
.select-wrapper select {
  cursor: pointer;
  padding: 7px 100px 5px 10px;
  border: 1px solid #f6cb5e;
  border-radius: 4px;
  background: #fff;
  height: 31px;
  -moz-appearance: none;
  -webkit-appearance: none;
  font-size: 13px;
  appearance: none;
  outline: none;
  display: block;
  color: #666;
  width: 100%;
}
.select-wrapper:after {
  cursor: pointer;
  content: "";
  height: 10px;
  width: 10px;
  background: url("../img/pictures/down-chevron.svg") center;
  background-size: cover;
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}
.checkbox-wrapper {
  cursor: pointer;
  display: block;
  margin-bottom: 0;
}
.checkbox-wrapper .checkbox-box {
  position: relative;
  display: flex;
  align-items: center;
  font-size: 15px;
  text-transform: none;
  color: #777;
  text-align: left;
  transition: all 0.3s;
}
.checkbox-wrapper .checkbox-box:before {
  content: "";
  min-width: 16px;
  max-width: 16px;
  height: 16px;
  display: block;
  background: url("../img/pictures/checkbox.svg") no-repeat;
  margin-right: 10px;
  transition: all 0.3s;
}
.checkbox-wrapper input {
  position: absolute;
  opacity: 0;
  width: auto !important;
}
.checkbox-wrapper > input:checked + .checkbox-box {
  font-weight: 600;
  color: #e7e6e4;
}
.checkbox-wrapper > input:checked + .checkbox-box:before {
  background: url("../img/pictures/checkbox-checked.svg") no-repeat;
}
nav.section-header {
  transition: all 0.5s;
  position: fixed;
  z-index: 555;
  top: 0;
  left: 0;
  width: 100%;
}
nav.section-header .header-management {
  padding: 25px 0;
  max-height: 200px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: all 0.5s;
}
nav.section-header .header-management .management-links {
  transition: all 0.5s;
}
nav.section-header .header-management .management-links ul {
  display: flex;
  align-items: center;
}
nav.section-header .header-management .management-links ul li a svg {
  width: 18px;
  height: 18px;
}
nav.section-header .header-management .management-links ul li + li {
  margin-left: 30px;
}
nav.section-header .header-management .management-links .btn-color {
  padding: 5px 30px;
}
nav.section-header .header-management .management-logo {
  text-align: center;
  opacity: 0;
  transition: all 1s;
}
nav.section-header .header-management .management-logo a {
  display: block;
  position: relative;
  transition: all 0.4s;
  transform: translateY(10px) translateZ(0);
  color: #f6cb5e;
  letter-spacing: 1px;
  font-size: 90px;
  line-height: 45px;
  font-family: "Italianno", cursive;
}
nav.section-header .header-management .management-logo a span {
  display: block;
  font-size: 12px;
  line-height: 12px;
  font-weight: 700;
  letter-spacing: 1.5px;
  color: #000;
  font-family: "Cormorant", serif;
  text-transform: uppercase;
  transition: opacity 350ms;
}
nav.section-header .header-management .management-logo.opacity {
  opacity: 1;
}
nav.section-header .header-management .management-burger-menu {
  transition: all 0.5s;
  display: flex;
  justify-content: flex-end;
  position: relative;
}
nav.section-header .header-management .management-burger-menu .line {
  display: inline-block;
  height: 10px;
  width: 1px;
  color: #000;
  margin: 0 10px;
}
nav.section-header .header-management .management-burger-menu p {
  font-size: 20px;
}
nav.section-header.scrolled {
  background: rgba(231, 230, 228, 0.6);
}
nav.section-header.scrolled .header-management {
  padding: 5px 0 15px;
}
nav.section-header.scrolled .header-management .management-logo a {
  transform: translateY(10px) scale(0.7);
}
nav.section-header.scrolled .header-management .management-logo a span {
  opacity: 0;
}
nav.section-header .flexbox-nav-wrapper {
  position: fixed;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100%;
  background: rgba(0, 0, 0, 0.3);
  z-index: -1;
  opacity: 0;
  visibility: hidden;
  overflow: hidden;
  transition: all 0.4s;
}
nav.section-header .flexbox-nav-wrapper.active {
  visibility: visible;
  opacity: 1;
  z-index: 111;
}
nav.section-header .flexbox-nav-wrapper.active .flexbox-nav-inner {
  transform: translateX(0);
}
nav.section-header .flexbox-nav-wrapper .flexbox-nav-inner {
  position: relative;
  left: 0;
  z-index: 55;
  height: 100%;
  width: 45%;
  transform: translateX(-100%);
  transition: all 0.4s;
  background-color: #fff;
}
nav.section-header .flexbox-nav-wrapper .flexbox-nav-inner .flexbox-nav {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  width: 100%;
  padding-left: 30%;
  transition: all 0.4s;
  transform: translateX(-200px);
}
nav.section-header .flexbox-nav-wrapper .flexbox-nav-inner .flexbox-nav.active {
  transform: translateX(0);
}
nav.section-header .flexbox-nav-wrapper .flexbox-nav-inner .flexbox-nav .img-absolute {
  position: absolute;
  z-index: -1;
  top: 20%;
  left: -5%;
  width: 100%;
}
nav.section-header .flexbox-nav-wrapper .flexbox-nav-inner .flexbox-nav li a {
  font-size: 20px;
  display: inline-block;
  padding: 5px 0;
  color: #000;
}
nav.section-header .flexbox-nav-wrapper .flexbox-nav-inner .flexbox-nav li a.highlight {
  font-weight: 700;
  font-size: 28px;
}
nav.section-header .flexbox-nav-wrapper .flexbox-nav-inner .flexbox-nav li.line {
  background-color: #000;
  width: 50%;
  height: 1px;
  margin: 20px 0 10px;
}
.section-wrapper {
  max-width: 1400px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}
section.section-welcome {
  position: relative;
  padding-top: 130px;
}
section.section-welcome .wrapper-inner {
  position: relative;
  min-height: 75vh;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
section.section-welcome .wrapper-inner:before {
  content: "";
  position: absolute;
  top: -200px;
  left: -500px;
  width: 970px;
  height: 130%;
