  background-size: cover;
  opacity: 0.4;
}
section.section-welcome .wrapper-inner header {
  position: relative;
  padding-right: 170px;
}
section.section-welcome .wrapper-inner h1 {
  font-size: 100px;
  font-weight: 600;
  line-height: 1.1;
  letter-spacing: 1.5px;
  margin-bottom: 20px;
}
section.section-welcome .wrapper-inner h1 p {
