}
section.section-cta .cta-inner {
  position: relative;
  background-color: #fff;
  box-shadow: 0px 20px 40px 1px rgba(155, 155, 155, 0.25);
  width: 100%;
  min-height: 300px;
  padding: 80px 50px;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
section.section-cta .cta-inner .cta-image {
  object-fit: contain;
