section.section-about .text-absolute {
  font-family: "Italianno", cursive;
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  transform: translate(20%, 60%);
  font-size: 250px;
  color: #f6cb5e;
  opacity: 0.3;
}
section.section-about header h2 {
  color: #f6cb5e;
  font-family: "Italianno", cursive;
  font-size: 100px;
  line-height: 60px;
  letter-spacing: 1.5px;
