section.section-about .about-inner .inner-element header {
  margin-bottom: 40px;
}
section.section-about .about-inner .inner-element header h3 {
  font-size: 22px;
  line-height: 32px;
  font-weight: 600;
}
section.section-about .about-inner .inner-element p {
  font-weight: 400;
